* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Inter, "Open Sans", "HelveticaNeue-Light";
  overflow-anchor: none;
  scroll-behavior: smooth;
}

body {
  box-sizing: border-box;
  zoom: normal;
  zoom: 1;
  scroll-behavior: smooth;
  overflow-anchor: none;
  font-family: "Open Sans", sans-serif;
  line-height: 1.25;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  /* height: 100vh; */
  width: 100vw;
  overflow-x: hidden;
}

#hamburger-menu {
  width: 350px;
  height: 200px;
  border: none;
  background-color: #a7a7a7;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  display: none;
  margin: 0;
  padding: 0;
  z-index: 4;
}
#sidebar-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
}
