  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  list-style-type: none;
  width: 100%;
  height: 100%;
  padding-top: 10px;
  padding-bottom: 0px;
  padding-left: 0;
}
#sidebar-menu > ul > li {
  padding: 4px;
  width: 100%;
  height: 100%;
  text-align: center;
}
